export default {
	data() {
		return {
			FoodClassifyList: [],
			tabCur:1,
			FoodList:[],
			urlV:''+new Date().getTime()
		}
	},
	onLoad(){
		this.getFoodClassifyList()
	},
	methods:{
		getFoodClassifyList(){
			this.$request({
				url:"/front/food/queryFoodClassifyList"
			}).then(res=>{
				this.FoodClassifyList=res.data.data
			})
			this.getFoodList(this.tabCur)
		},
		getFoodList(id){
			this.tabCur=id
			this.$request({
				url:"/front/food/queryFoodListByClassifyId",
				data:{
					page:1,
					limit:10,
					classifyId:id 
				}
			}).then(res=>{
				console.log(res)
				this.FoodList=res.data.data
			})
		},
		ToMicrDetail(id){
			uni.navigateTo({
				url:'/pages/micrDetail/micrDetail?id='+id
			})
		}
	}
}
